
.notification {
  position: absolute;
  top: -15vh;
  -webkit-transition: .3s;
  transition: .3s;
  width: 100vw;
  min-height: 10vh;
  margin: auto;
  display: block;
  z-index: 6000;
}
.notification__active {
    -webkit-transition: .3s;
    transition: .3s;
    -webkit-transform: translate3d(0, 17vh, 0);
            transform: translate3d(0, 17vh, 0);
}
.notification__inset {
    position: relative;
    width: 95%;
    height: 100%;
    border-radius: 10px;
    margin: auto;
    background: #3d3e39;
    -webkit-box-shadow: 0px 0px 35px 0px #21211e;
    box-shadow: 0px 0px 35px 0px #21211e;
}
.notification-dots {
    height: 7vw;
    padding-right: 10px;
    border-right: 1px solid rgba(252, 252, 252, 0.3);
}
.notification-dots-wrapper {
      width: 15%;
      margin: 10px;
}
.notification__close {
    font-size: 12px;
    color: #ed3721;
    padding: 5px;
}
.notification__close-circle {
      position: absolute;
      top: 2px;
      right: 2px;
      margin: 5px;
      border-radius: 50%;
      border: 1px solid #ed3721;
}
.notification__message-wrapper {
    margin-right: 26px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 5px;
    font-size: 4vw;
}


















































































































































































































































































































































































































































































































































































.added-to-fav{
    position: absolute;
    bottom: 15vw;
    left: 30vw;
    opacity: 0;
    pointer-events: none;
    width: 40vw;
    height: 12vw;
    background: gray;
    border-radius: 20vw;
    -webkit-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
    box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
    z-index: 4000;
    -webkit-transition: .5s;
    transition: .5s;
    pointer-events: none;
}
.added-to-fav__show{
    position: absolute;
    bottom: 15vw;
    left: 30vw;
    opacity: 1;
    pointer-events: none;
    width: 40vw;
    height: 12vw;
    background: #3d3e39;
    border-radius: 20vw;
    -webkit-box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
    box-shadow: 0px 0px 80px 12px rgba(0,0,0,.5);
    z-index: 4000;
    -webkit-transition: .3s;
    transition: .3s;
    pointer-events: inherit;
}
.added-to-fav-text{
    font-size: 3vw;
    font-weight: 700;
}
.added-to-fav-text-i{
    color: #8a7c0d;
    margin-left:1vw; 
    font-size: 3vw;
}

.bkonloader-logo{
    position: absolute;
    width:40%;
    top: 40%;
    left: 30%;
    z-index: 100;
}
.bkonloader-track{
    width: 100%; 
    height:100%;
    -webkit-animation: rotate 2.5s infinite ;
            animation: rotate 2.5s infinite ;
    opacity: 0;
    border-radius: 50%;
    position: relative;
    border: 1px solid transparent;
    z-index: 500;
}
